<template>
  <div class="borrow--container">
    <div class="header--content">
        <span class="text">我要借款</span>
    </div>
    <div class="table--container">
      <section class="table-cell--content">
        <img class="cell--icon" src="https://om536p71r.qnssl.com/%E6%94%AF%E4%BB%98%E6%96%B9%E5%BC%8F.png" alt="">
        <input class="right-part" type="number" name="amount" min="1" max="100000" placeholder="请输入需借款数字(元)"></input>
      </section>
      <section class="table-cell--content">
        <img class="cell--icon" src="https://om536p71r.qnssl.com/%E6%94%AF%E4%BB%98%E6%96%B9%E5%BC%8F%20%281%29.png" alt="">
        <select class="selection">
          <option value ="cash">现金</option>
          <option value ="credit_card">银行卡</option>
          <option value="alipay">支付宝</option>
          <option value="wechatpay">微信支付</option>
        </select>
      </section>
    </div>
    <div class="center-btn--area">
      <div @click="dialogStatus = true" class="global--button_primary radius">确认借款</div>
    </div>
    <div class="popup-dialog--container"  :class="dialogStatus ? 'active' : ''">
      <div class="mask"></div>
      <div class="dialog--main-container">
        <div class="dialog--header"></div>
        <div class="dialog--body">
          <i class="iconfont body--icon">&#xe675;</i>
          <div class="body--desc">您的借款申请已提交至客服</div>
        </div>
        <div class="dialog--footer">
          <div @click="confirmHandler" class="global--button_primary">确定</div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        dialogStatus: false,
      };
    },
    mounted() {
    },
    methods: {
      confirmHandler() {
        this.dialogStatus = false;
      },
    },
  };
</script>



