<template>
  <div class="borrow--list--container">
    <div class="header--content">
      <img src="https://om536p71r.qnssl.com/%E5%A4%B4%E5%83%8F.png" alt="" class="avatar">
    </div>
    <div class="main-content--container">
      <div class="content--header">
        <span @click="changeTab('left')" class="left-part" :class="tabStatus == 'left' ? 'active' : ''">已还款</span>
        <span @click="changeTab('right')" class="right-part" :class="tabStatus == 'right' ? 'active' : ''">未还款</span>
      </div>
    </div>
    <div class="body-content--container" v-if="tabStatus == 'left'">
      <div class="table--container">
          <table class="table">
              <tr class="head">
                  <th>还款时间</th>
                  <th>姓名</th>
                  <th>手机号码</th>
                  <th>已还金额</th>
              </tr>
              <tr class="element">
                  <td>2017-12-12</td>
                  <td>阳阳</td>
                  <td>123456789</td>
                  <td>5元</td>
              </tr>
              <tr class="element">
                  <td>2017-12-12</td>
                  <td>阳阳</td>
                  <td>123456789</td>
                  <td>5元</td>
              </tr>
          </table>
      </div>
    </div>
    <div class="body-content--container" v-if="tabStatus == 'right'">
      <div class="body--price-num">6.00</div>
      <div class="body--desc">最后还款日 12月12日</div>
      <div class="body--btn-area">
        <div @click="dialogStatus = true" class="global--button_primary">提前还款</div>
      </div>
    </div>
    <div class="popup-dialog--container"  :class="dialogStatus ? 'active' : ''">
      <div class="mask"></div>
      <div class="dialog--main-container">
        <div class="dialog--header"></div>
        <div class="dialog--body">
          <i class="iconfont body--icon">&#xe675;</i>
          <div class="body--desc">您的还款申请已提交至客服</div>
        </div>
        <div class="dialog--footer">
          <div @click="confirmHandler" class="global--button_primary">确定</div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        tableHead:[],
        tableElement:[],
        tabStatus: 'right',
        dialogStatus: false,
      };
    },
    mounted() {
    },
    methods: {
      changeTab(params) {
        this.tabStatus = params;
      },
      confirmHandler() {
        this.dialogStatus = false;
      },
    },
  };
</script>



